	module.exports = {"data":{"markdownRemark":{"html":"<p>Let grasses grow\nLet waters flow\nIn a free and easy way\nBut, give me enough\nOf the rare old stuff\nThey brewed near galway bay</p>\n<p>Come policeman all from Donegal, Kill Devil, and Smithville too\nOh, well give them the slip and we'll take a sip\nOf the Rare Auld Mountain Dew</p>\n<p>Theres a neat little still\nAt the foot of the hill\nWhere the smoke curls far away\nYou can surely smell\nThe potent swell\nFrom across the Galway Bay</p>\n<p>For it fills the air\nwith a perfume rare\nand betwixt both me and you\nas home we troll, we can take a bowl\nor a bucket of The Mountain Dew</p>\n<p>As learned men, we use the pen\nTo sing our praises high\nOf the rare potion, of Emerald green\nDistilled from wheat and rye\nPut away your pills, it'll cure all ills\nBe ye Uncles and Mothers too\nSo take off your coat and grease yer throat\nWith a bucket of The Mountain Dew</p>","frontmatter":{"title":"The Rare Auld Mountain Dew"},"fields":{"date":null}}},"pathContext":{"slug":"/songs/rare-auld-mountain-dew/"}}

